
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {httpUrl} from '../../http_url';
import 'vue-img-inputer/dist/index.css'

export default {
directives: {
TransferDom
},
data () {
return {
show: false,
menus: {
menu1: '退出登录'
},
showMenus: false,
username:'username',
bankCardFrontImg:'',
bankCardFrontUrl:'./static/imgs/photo_icon.png',
bankCardBackImg:'',
bankCardBackUrl:'./static/imgs/photo_icon.png',
display:'true',
show:'true',
bcNo:'',
}
},
created(){
this.infoCheck();
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
Alert,
Toast,
Divider,
Masker,
Group,
XInput
},
methods:{
infoCheck(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.informationCheck,
}).then((res)=>{
console.log(res.data)
let data = res.data.retData;
if(res.data.errCode==200){
let personType = data.person;
let bankType = data.bank;
if(personType == 0){
this.$vux.alert.show({
title: '错误提示',
content: '未实名认证，请先实名认证',
onHide () {
_this.$router.push('/certification');
}
})
return
}else if(bankType == 1){
this.$vux.alert.show({
title: '错误提示',
content: '银行卡已验证！',
onHide () {
_this.$router.push('/credit_info_list');
}
})
}
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
getData(){
const _this = this;
let postData = this.$qs.stringify({
page:1
})
this.$axios({
method: 'post',
url:httpUrl.userInformation,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.username = res.data.retData.name;
}else if(res.data.errCode==201){
this.$vux.toast.show({
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
showPlugin () {
this.$vux.alert.show({
title: '错误提示',
content: '请上传银行卡正面图片',
})
},
showAlert(errText){
const _this = this
this.$vux.confirm.show({
title:'提示',
content:errText,
cancelText:'取消',
confirmText:'手动银行卡认证',

onCancel () {

},
onConfirm () {
_this.$router.push('/bank_edit');
}
})
},
onChangeFront(file){
const self = this;
var reader = new FileReader();
reader.readAsDataURL(file);
reader.onloadend = function() {
let result = reader.result;
let img = new Image();
img.src = result;

if (this.result.length <= (100 * 1024)) {
self.upLoadFrontImg(reader.result,2)
}else {
img.onload = function () {
let src = self.compress(img);
self.upLoadFrontImg(src,2)
}
}
};
},
onChangeBack(file){
const self = this;
var reader = new FileReader();
reader.readAsDataURL(file);
reader.onloadend = function() {
let result = reader.result;
let img = new Image();
img.src = result;

if (this.result.length <= (100 * 1024)) {
self.upLoadBackImg(reader.result,3)
}else {
img.onload = function () {
let src = self.compress(img);
self.upLoadBackImg(src,3)
}
}
};
},

upLoadFrontImg(base64, type){
const _this = this;
this.$vux.loading.show({
text: '正在上传...'
})
let postData = this.$qs.stringify({
typeid:type,
image:base64
})
this.$axios({
method: 'post',
url:httpUrl.bankUploads,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data);
this.$vux.loading.hide();
if(res.data.errCode==200){
this.$vux.toast.show({
text: '上传成功',
type:'success',
time:2000
})
this.bcNo = res.data.retData.bank_number;
this.show = 'success1';
}else if(res.data.errCode==201){
this.showAlert(res.data.retMsg);
this.bankCardFrontImg = '';

}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},

upLoadBackImg(base64, type){
const _this = this;
this.$vux.loading.show({
text: '正在上传...'
})
let postData = this.$qs.stringify({
typeid:type,
image:base64
})
this.$axios({
method: 'post',
url:httpUrl.bankUploads,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data);
this.$vux.loading.hide();
if(res.data.errCode==200){
this.$vux.toast.show({
text: '上传成功',
type:'success'
})
this.display = "success2";
}else if(res.data.errCode==201){
this.showAlert(res.data.retMsg);
this.bankCardBackImg = '';

}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
nextStep(){
const step_type = this.$route.query.step_type;
const orderid = this.$route.query.orderid;
const _this = this;

if(this.isEmpty(this.bankCardFrontImg)){
this.showPlugin();
return
}if(this.isEmpty(this.bankCardBackImg)){
this.$vux.alert.show({
title: '错误提示',
content: '请上传银行卡反面图片',
})
return
}else {
if(step_type == '1'){
_this.$router.push({
path:'/bank_edit',
query: {
step_type:step_type,
orderid:orderid
}
});
}else {
_this.$router.push({
path:'/bank_edit'
});
}

}
},
goHandle(){
this.$router.push('/handle_certification');
},
goHome(){
this.$router.push('/home');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}
}
}



